import React from 'react';
import Footer from './../components/Footer';
import Layout from './../components/layout';
import Container from './../components/Container';
import Row from './../components/Row';

const BlogTag = (props) => (
	<li>
		<a href="#">{`${props.name}, `}</a>
	</li>
);

const BlogPost = (props) => {
	const blogTitle = props.blogTitle;
	const blogSubtitle = props.blogSubtitle;
	const blogExcerpt = props.blogExcerpt; // not in use, yet
	const blogSrc = props.blogSrc;
	const tags = props.tags;
	const domain = props.blogDomain;
	const date = props.blogDate;
	return (
		<div className="blog-post">
			<img src="https://s3.eu-central-1.amazonaws.com/berlin-robotics/upload/blog/large1.jpg" alt="" />
			<span className="date-comments">{date} - <i>{domain}</i></span>
			<h2><a href={blogSrc} target="_blank" rel="noopener noreferrer">{blogTitle}</a></h2>
			<p>{blogSubtitle}</p>
			<Row>
				<div className="col-md-6">
					<a
						href={blogSrc}
						className="continue-read"
						target="_blank"
						rel="noopener noreferrer"
					>
						Continue Reading <i className="fa fa-angle-right"></i>
					</a>
				</div>
				<div className="col-md-6">
					<ul className="post-tags">
						{tags.map((tag, index) => <BlogTag key={index} name={tag.name} />)}
					</ul>
				</div>
			</Row>
		</div>
	);
};

